@import "../../style/colors.scss";
@import "../../style/metrics.scss";

.about-container {
    display: flex;
    flex: 1;
    width: inherit;
    //height: 1080px;
    background-color: $primary-dark;
    align-items: center;
    justify-content: center;
    padding: 100px 30px 100px 30px;
}

.col1-about {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.col2-about {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.description {
    color: $title;
    font-style: normal;
    font-weight: normal;
    text-align: justify;
    font-size: 18px;
    line-height: 21px;
}

.page-title {
    padding-bottom: 20px;
    font-style: normal;
    font-weight: bold;
    font-size: 34px;
    line-height: 40px;
    color: $secondary;
}

.title-about {
    color: $secondary;

    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
}

.about-img {
    width: 420px;
    height: 570px;
}

.key {
    color: $heading;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
}

.value {
    color: $title;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
}

.about-progress-rootContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.about-progress-container{
    display: flex;
    width: fit-content;
    flex-direction: column;
    align-items: center;
    //margin-right: 25px;
}

.about-progress{
    color: $secondary;
}

.about-progress-title{
    color: $title;
}

#download-cv-but {
    position: relative;
    color: $secondary;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    background-color: $primary-dark;
    border: 1px solid $secondary;
    border-radius: $baseRadius;
    width: 150px;
    height: 50px;
    box-shadow: none;
    z-index: 1;
}

#download-cv-but:hover {
    color: $primary-dark;
    transition: all 0.3s;
}

#download-cv-but::after {
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background-color: $secondary;
    content: "";
    opacity: 0;
    transition: all 0.3s;
    z-index: -1;
}

#download-cv-but:hover::after {
    width: 100%;
    opacity: 1;
    transition: all 0.3s;
}

.key-container {
    padding-right: 0px;
}

.value-container {
    padding-left: 0px;
}
@media only screen and (max-width: $mobileScreen) {
    .col1-about {
        display: none;
    }

    .page-title {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 28px;
        color: $secondary;
    }

    .title-about {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
    }

    .description {
        font-size: 18px;
    }

    .key,
    .value {
        font-size: 16px;
    }

    #download-cv-but {
        width: 150px;
        height: 50px;
    }
}
